import React, { Component } from 'react';
import { reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { CssBaseline, Typography, Container, Button, createMuiTheme } from '@material-ui/core';
import { withStyles } from "@material-ui/styles";
import Image from 'react-bootstrap/Image';
import { signin } from '../../../actions/user';

const theme = createMuiTheme({
    spacing: 4
});

const styles = {
    title: {
        marginLeft: theme.spacing(-100),
    },
    logo: {
        width: 150,
        height: 50

    },

    signup: {
        marginTop: theme.spacing(-20),
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        marginLeft: theme.spacing(100),
        width: 500,
    },
    paper: {
        marginTop: theme.spacing(50),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    form: {
        width: '85%', // Fix IE 11 issue.
        marginTop: theme.spacing(5),
        borderRadius: 0,
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    button: {
        backgroundColor: '#19b4ff',
        marginTop: theme.spacing(3),
    },
};

class Welcome extends Component {
    constructor() {
        super();
        this.gotoLoginButtonClick = this.gotoLoginButtonClick.bind(this);

    }

    gotoLoginButtonClick() {
        return this.props.history.push('/login/');
    }

    render() {
        const { classes } = this.props;
        return (
            <Container component="main" maxWidth="xs">
                <div className={classes.title}>
                    <h1>
                        <Image src="../../../../images/logo.png" fluid className={classes.logo} />
                    </h1>
                </div>
                <CssBaseline />

                <div className={classes.signup}>
                    <form className={classes.form} >

                    </form>
                </div>

                <div className={classes.paper}>
                    <Typography component="h1" variant="h5" style={{ fontSize: '50px', marginRight: 63 }}>
                        Welcome!
                    </Typography>
                    <form className={classes.form} >
                        <Button
                            style={{ marginLeft: 80, marginTop: 3, backgroundColor: '#f80066', color: '#ffffff', fontWeight: 'bold', width: 100, height: 30, textTransform: 'none' }}
                            onClick={this.gotoLoginButtonClick}
                        >
                            Back
            </Button>
                    </form>
                </div>
            </Container>
        );
    }
}

function mapStateToProps({ auth }) {
    return {
        message: auth.message
    }
}

export default connect(mapStateToProps, { signin })(reduxForm({
    form: 'first'
})(withStyles(styles)(Welcome)));
